
import type { AnimationItem } from 'lottie-web';
import React from 'react';
import { useEffect, useRef } from 'react';
import suppliesJson from './blackboard.json';
import MockupBg from './hero-mockup.jpg'
import QuickHeader from '../../components/QuickHeader';


const BasicLottie = ({ animationData, height }: { animationData: object, height?: number }) => {
  const containerRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    // This only runs in the browser
    if (typeof window === 'undefined') return;

    // Import lottie dynamically
    import('lottie-web').then((lottie : any ) => {
      const anim = lottie.loadAnimation({
        container: containerRef.current!,
        renderer: 'svg',
        loop: true,
        autoplay: true,
        animationData,
      });

      return () => anim.destroy();
    });
  }, [animationData]);

  if (typeof window === 'undefined') return null;

  return (
    <div
      ref={containerRef}
      style={{
        width: '100%',
        height: height ?? '100%',
      }}
    />
  );
};



export default () => <>
    <QuickHeader/>
    <img src={MockupBg} width="1080px"/>
    <div style={{ position : 'absolute', top : 204, left : 1040 - 470, width : 500, height : 500  }}>
        <BasicLottie animationData={suppliesJson} height={500}/>
    </div>
</>


